/**
 * File app/middleware/access-key-middleware.js
 *
 * Ce fichier contient le middleware pour l'accs  l'app
 *
 * @author sofianeakbly
 **/

import access      from "./access";
import { validIp } from "./index";

/**
 * Vrifie si l'accs  l'app est autoris
 *
 * @param to La route suivante
 * @param from La route de provenance
 * @param next La requte suivante
 **/

export default async (to, from, next) => {
	const store = require('app/vue/store').default,
		cookieKey = store.getters.cookieAccessKey,
		token = localStorage.getItem(cookieKey),
		notDev = store.getters.APP_ENV !== 'dev'

	if (notDev && !token) {

		if (notDev) {
			let valid = await validIp(process.env.ALLOWED_IP)

			if (valid)
				return next()
			else return next({ name: 'access.scanner' })
		} else
			return next({ name: 'access.scanner' });
	}

	access
		.all()
		.then(() => next())
		.catch(() => {
			if (notDev && token) {
				localStorage.removeItem(cookieKey)
				return location.reload()
			}
			return next()
		})
}

