<template>
	<ssf-container name="access-scanner">
		<qr-scanner @scan="extract"/>
	</ssf-container>
</template>

<script>
	import QrScanner   from "./qr-scanner";
	import access      from "../utils/access";
	import { validIp } from "../utils";

	export default {
		name      : "access-scanner",
		components: { QrScanner },

		computed: {
			storageKey() {
				return this.$store.getters.cookieAccessKey
			},

			token() {
				return localStorage.getItem(this.storageKey)
			}
		},

		watch: {
			token: {
				immediate: true,
				async handler(val, oldVal) {
					let token = val

					if (token !== null && token.trim() === '' && await access.all().then(() => true).catch(() => false))
						return this.$router.push({ name: 'home' })

					if (await validIp(process.env.ALLOWED_IP))
						return this.$router.push({ name: 'home' })
				}
			}
		},

		methods: {
			extract(result) {
				const token = result.split('/').reverse()[0] || null

				if (token === null || token.trim() === '')
					return;

				setTimeout(() => {
					localStorage.setItem(this.storageKey, token)
					this.$router.push({ name: 'home' })
				}, 1000)
			}
		}
	}
</script>

