<template>
	<ssf-container name="qr-scanner" id="qrScanner" not-full class="position-relative full-flex">
		<div id="scanner" :class="{scanning}" class="position-absolute"><span></span></div>
		<div v-if="scanning" class="scanner-bar scanning"></div>
		<div v-if="scanned" class="scanned full-flex">
			<ssf-icon icon="check-circle" la size="6x" class="green-text"/>
		</div>
		<!--    <div class="qrPreviewVideo"></div>-->
	</ssf-container>
</template>

<script>

	import { JsQRScannerReady } from "../utils/jsqr";

	export default {
		name: "qr-scanner",

		props: {
			size: { type: Number | String, required: false, default: 200 }
		},

		data() {
			return {
				jbScanner: null,
				scanned  : false
			}
		},

		mounted() {

			this.$nextTick(() => window.addEventListener('scanned', this.onScanned))
			this.$nextTick(() => window.addEventListener('scannerReady', this.onScannerReady))
		},

		beforeDestroy() {
			window.removeEventListener('scanned', () => null);
			window.removeEventListener('scannerReady', () => null);
		},

		computed: {
			scanning() {
				return this.jbScanner ? this.jbScanner.isScanning() : false
			}
		},

		methods: {
			onScannerReady() {
				if ((this.jbScanner = JsQRScannerReady(this.size)) === null)
					alert('Cannot initialize camera');
				else {
					$('.qrPreviewVideo').attr('controls', false);
				}
			},

			onScanned(event) {
				const text = event && event.detail ? event.detail.text : null
				if (text && text.trim()) {
					this.scanned = true
					this.jbScanner.stopScanning()
					setTimeout(() => {
						this.$emit('scan', text)
						this.jbScanner.resumeScanning()
						this.scanned = false
					}, 2000)
				}
			}
		}
	}
</script>

<style scoped>

	#qrScanner {
		height: 100vh;
		width: 100vw;
		background: rgba(0, 0, 0, .8);
	}

	#qrScanner #scanner {
		height: 200px;
		width: 200px;
	}

	#qrScanner #scanner {
		position: relative;
		z-index: 2;
		border: 2px solid transparent;
	}

	#qrScanner #scanner:before, #qrScanner #scanner:after, #qrScanner #scanner span::before, #qrScanner #scanner span::after {
		display: block;
		content: "";
		width: 30px;
		height: 30px;
		position: absolute;
	}

	#qrScanner #scanner:before {
		top: -2px;
		left: -2px;
		border-top: 1px solid #ffa800;
		border-left: 1px solid #ffa800;
		border-top-left-radius: 6px;
	}

	#qrScanner #scanner:after {
		top: -2px;
		right: -2px;
		border-top: 1px solid #ffa800;
		border-right: 1px solid #ffa800;
		border-top-right-radius: 6px;
	}

	#qrScanner #scanner span::before {
		bottom: -2px;
		left: -2px;
		border-bottom: 1px solid #ffa800;
		border-left: 1px solid #ffa800;
		border-bottom-left-radius: 6px;
	}

	#qrScanner #scanner span::after {
		bottom: -2px;
		right: -2px;
		border-bottom: 1px solid #ffa800;
		border-right: 1px solid #ffa800;
		border-bottom-right-radius: 6px;
	}

	#qrScanner .scanner-bar.scanning {
		position: absolute;
		height: 150px;
		width: 2px;
		z-index: 3;
		background: #355935;
		animation: slide 2s infinite alternate;
	}

	#qrScanner .qrPreviewVideo {
		position: absolute;
		height: 198px;
		width: 198px;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		margin: auto !important;
		border-radius: 5px;
	}

	#qrScanner .scanned {
		position: absolute;
		height: 198px;
		width: 198px;
		border-radius: 5px;
		z-index: 2050;
		background: rgba(0, 0, 0, .6);
	}

	@keyframes slide {
		from {
			-webkit-transform: translateX(-100px);
			-moz-transform: translateX(-100px);
			transform: translateX(-100px);
		}
		to {
			-webkit-transform: translateX(100px);
			-moz-transform: translateX(100px);
			transform: translateX(100px);
		}
	}

</style>
